import React from 'react'
import { categoriesData } from '../../data'
import { GridView, Slider } from "../index"
import { useTranslation } from "react-i18next"
import { Helmet } from 'react-helmet-async'

const MainPage = () => {
    const { t } = useTranslation()
    return <>
        <Helmet>
            <title>   </title>
            <meta
                name="description"
                content="    "
            />
            <link href="https://feya-bilyzna.github.io/development/#/" />
        </Helmet>
        <Slider />
        <h3 style={{ "textAlign": "center", margin: 30 }}>{t(" ")}</h3>
        <GridView cardItems={[
            ...Object.values(categoriesData.categories),
            ...Object.values(categoriesData.uncategorizedSubcategories),
        ]} />
    </>
}

export default MainPage
